.App {
  text-align: center;
  color: var(--main-text-color);
}

.App-header {
  background-color: var(--main-bg);
  /* transition: background-color 10s; */
  min-height: 100vh;
  display: flex;
  flex-direction: row;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}

/* .App-header.hide {
  display: none;
} */

.main-column {
  min-height: 100vh;
  width: 100vw;
  max-width: 500px;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  justify-content: flex-start;
}

.game-header, .modal-header {
  display: flex;
  flex-direction: row;
  justify-content: stretch;
  align-items: center;
  flex: 0 0 50px;
  z-index: 1;
}

.game-header {
  border-bottom: 2px solid var(--main-bg);
  position: sticky;
  /* background-color: var(--main-bg); */
  /* background: linear-gradient(var(--slash-angle), var(--header-main-color) 0%, var(--header-main-color) 50%, var(--header-secondary-color) 50%, var(--header-secondary-color) 100%); */
  background: var(--header-secondary-color);
  top:0;
}

.modal-header {
  margin-bottom: 12px;
}

.game-header-title {
  display: flex;
  flex: 1;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  flex: 4;
  font-size: 28pt;
  font-weight: bold;
  color: var(--header-main-color);
}

.game-header-title.left {
  justify-content: flex-end;
  color: var(--header-secondary-color);
  /* background-color: rgba(0, 0, 255, 0.5); */
}

.game-header-title.left > div {
  padding-right: 0.17em;
}

.game-header-title.right {
  justify-content: flex-start;
  color: var(--header-main-color);
  /* background-color: rgba(255, 0, 0, 0.5); */
}

.game-header-title sup {
  font-size: 18pt;
}

.modal-title {
  display: flex;
  flex-direction: row;
  /* justify-content: flex-start; */
  justify-content: center;
  align-items: center;
  flex: 4;
  font-size: 16pt;
  font-weight: bold;
}

.game-header-button, .modal-close-button {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  align-self: stretch;
  flex: 0 50px;
  overflow: hidden;
  background: none;
  border: 0;
  font-size: 18pt;
  font-weight: bold;
  color: var(--main-text-color);
}

.game-header svg {
  fill: var(--header-main-color);
  flex: 1;
  min-width: 22px;
  min-height: 22px;
}

