.modal.hide {
  display: none;
}

.modal.show, .modal .shade {
  position: fixed;
  overflow: auto;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  left: 0;
  top: 0;
  height: 100vh;
  width: 100vw;
  /* background-color: rgba(0, 0, 0, 0.5); */
  background-color: var(--main-bg);
  z-index: 2;
}

