  justify-content: center;
  background: none;
}

.modal.rules .shade {
  background-color: rgba(0, 0, 0, 0.5);
}

.modal.rules.show .main-column {
  background-color: var(--header-secondary-color);
  width: 90vw;
  max-width: 450px;
  min-height: calc(100vh - 10vw);
  height: calc(100vh - 50px);
  border-radius: 10px;
  overflow: auto;
  z-index: 3;
}

